<template>
    <v-container class="full-height">
        <v-row>
            <v-col cols="12">
                <v-select v-model="selectedTitle" :items="titles" label="Select Message Type" outlined></v-select>
            </v-col>
        </v-row>
        <v-row class="full-height">
            <v-col>
                <p v-html="selectedMessage"></p>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
export default {
    name: 'saleTalk',
    data() {
        return {
            currentContact: null, // Assuming you receive 'currentContact' from navigation
            selectedTitle: 'Project Ashwood', // Default selected title
            titles: ['Project Ashwood', 'Project Titan', 'For Sales & Rent', 'Exclusive Opportunity at RBJ Condominium'], // Title options
            messages: {
                'Project Ashwood': `page 1
<b>Ashwood: Your Gateway to Urban Living</b><br/>
Hi @name, how are you, Previously, you inquired about <b>@project</b>, our newly launched condominium at Jalan Uthant. Strategically located just 5 minutes from KLCC and 10 minutes from TRX, Ashwood offers the perfect blend of convenience and tranquility.<br/><br/>
Nestled amidst prestigious educational institutions like Flose International School and SJKK Chung Hwa(p) School, @project provides a prime location for families and individuals seeking a high-quality lifestyle.<br/><br/>
<b>Exclusive Event This Saturday</b><br/>
Join us this Saturday for a private event where you can explore our remaining 30 units. All units are corner units, ensuring breathtaking views of KLCC or Bukit Ampang.<br/>
Whether you're looking for a dream home or a lucrative investment, @project presents an exceptional opportunity.<br/><br/>
            <b>Reseller Program</b><br/>
We invite you to participate in our reseller program and earn referral fees for every successful sale.<br/><br/>`,
'Project Titan': `page 2, Previously, you inquired about <b>@project</b>, our newly launched condominium at Bukit Jalil. Are you considering it for investment or home stay?
           for stay I recommend @project,
           Strategically located just 5 minutes to Pavillion  and 5 minutes from LRT, @project offers the perfect blend of convenience and tranquility.<br/><br/>
Nestled amidst prestigious educational institutions like Chi Ji International School, @project provides a prime location for families and individuals seeking a high-quality lifestyle.<br/><br/>
<b>Exclusive Event This Saturday</b><br/>
Join us this Saturday for a private event where you can explore our remaining 30 units. All units are corner units, ensuring breathtaking views of KLCC or Bukit Ampang.<br/>
Whether you're looking for a dream home or a lucrative investment, @project presents an exceptional opportunity.<br/><br/>
<b>Reseller Program</b><br/>
We invite you to participate in our reseller program and earn referral fees for every successful sale.<br/><br/>`,
           'For Sales & Rent': `page 3
<b>@project: Your Gateway to Urban Living</b><br/>
Hi @name how are you, I understand that you have a unit at @project. we have a potential customer who are interested in the unit, are you considering selling or Rent? We offer complimentary advertising services to help you generate income from your property. Our team is ready to assist you.<br/>
We offer a hassle-free process to help you with the sale. Let us know how we can assist.<br/>
To proceed, may I know if the unit is furnished or unfurnished? Could you also let us know the floor it's located on and the square footage of the unit? These details would be helpful for the potential tenant or buyer.
Thank you, and we're looking forward to assisting you further!",
            `,
"Exclusive Opportunity at RBJ Condominium":`

Thank you for your previous inquiry about RBJ, our newly launched condominium in Bukit Jalil. Are you considering it as an investment or for your home?

If you're looking for a place to live, RBJ is the perfect choice. Located just 5 minutes from Pavilion and the LRT, it offers an unbeatable combination of convenience and tranquility. Imagine living in a home where everything you need is just a few minutes away—shops, restaurants, schools, and easy transportation access.

Plus, with its serene surroundings and modern amenities, RBJ offers a high-quality lifestyle for both families and individuals. Whether you're thinking about upgrading your lifestyle or securing a valuable asset for the future, RBJ is a smart move.
`

            },
            formattedText: "",
        };
    },
    created() {
        const contactString = this.$route.query.contact;
        let contact = null;

        if (contactString) {
            try {
                contact = JSON.parse(contactString);
            } catch (e) {
                console.error('Failed to parse contact string:', e);
            }
        }

        let name = "";
        let project = "";

        if (contact) {
            name = contact.name;
            project = contact.project;

            if (project && name) {
                // Update formattedText based on selected title and contact details
                this.formattedText = this.buildPersonalizedText(project, name, this.selectedTitle);
            }
        }
    },
    watch: {
        // Watch for changes in the selected title to update the displayed message
        selectedTitle(newTitle) {
            const contactString = this.$route.query.contact;
            let contact = null;

            if (contactString) {
                try {
                    contact = JSON.parse(contactString);
                } catch (e) {
                    console.error('Failed to parse contact string:', e);
                }
            }

            if (contact) {
                this.formattedText = this.buildPersonalizedText(contact.project, contact.name, newTitle);
            }
        }
    },
    computed: {
        selectedMessage() {
            return this.formattedText; // Return the formatted text for display
        }
    },
    methods: {
        buildPersonalizedText(project, name, title) {
            // Modify the text to personalize based on project, name, and selected title
            let personalizedText = this.messages[title];
            personalizedText = personalizedText.replaceAll('@name', name); // Replace placeholder with name
            personalizedText = personalizedText.replaceAll('@project', project); // Replace placeholder with project
            return personalizedText;
        },
    },
};
</script>

<style scoped>
.full-height {
    height: 100vh;
}

p {
    white-space: pre-wrap;
    word-break: break-word;
    overflow-y: auto;
    max-height: 100%;
}
</style>
